/*
 * This directive manages the start and stop of observation
 */
angular.module('acedIntervention.directives').directive('observationManager',function ($rootScope) {
    return {
        templateUrl: 'template/observationManager.html',
        scope:{

        },
        restrict: 'EA',
        controller: function ($scope) {
            $scope.observationStarted = false;

            $scope.startObservation = function() {
                $scope.$broadcast('timer-start');
                $scope.observationStarted = true;
                $scope.$emit('observationManager-timerStarted');
            };

            $scope.endObservation = function() {
                $scope.$broadcast('timer-stop');
                $scope.observationStarted = false;
            };

            $scope.$on('timer-stopped', function (event, data){
                $scope.$emit('observationManager-timerStopped',data);
            });

            $scope.cancelObservation = function() {
                console.log($rootScope.previousState);
                $rootScope.goTo($rootScope.previousState.state.name,$rootScope.previousState.params)
            }
            $scope.$on('$destroy', $scope.destroy);
        },
        link: function (scope, element, attrs, ctrl) {
        }
    }
});

/*
 * This directive measure and emits the frequency and duration of a behavior
 */
angular.module('acedIntervention.directives').directive('measureFAndD',['utilsService',function (utilsService) {
    return {
        templateUrl: 'template/measureFAndD.html',
        scope : {
            enableObservation : '=',
            behavior : '=',
            realTime : '='
        },
        restrict: 'EA',
        controller: function ($scope) {
            $scope.durationStarted = false;
            $scope.durationCounter = 0;
            $scope.frequencyCounter = 0;

            $scope.startDuration = function() {
                $scope.$broadcast('timer-start');
                $scope.durationStarted = true;
            };

            $scope.endDuration = function() {
                $scope.$broadcast('timer-stop');
                $scope.durationCounter++;
                $scope.durationStarted = false;
            };

            // emit that the counter changed each time the user hits + or -
            $scope.changeFrequencyCounter = function(operator) {
                if (operator=='add') $scope.frequencyCounter++;
                if (operator=='subtract') $scope.frequencyCounter--;
                var frequencyData = {
                    targetBehavior:$scope.behavior.title,
                    frequency:$scope.frequencyCounter
                };
                $scope.$emit('frequency-counterChanged',frequencyData);
            };

            // listen to event from controller to stop the timer
            $scope.$on('durationTimer-stopTimer',function(event,data){
                console.log('durationTimer-stopTimer event listener');
                // stop the timer when it has started.
                if ($scope.durationStarted === true) {
                    $scope.$broadcast('timer-stop');
                }
            });

            // listener for timer stopped event , triggered  when timer stops
            $scope.$on('timer-stopped', function (event, data){
                console.log('timer-stopped event listener');
                var durationData = {
                    targetBehavior:$scope.behavior.title,
                    duration:data.millis
                };
                $scope.$emit('durationTimer-timerStopped',durationData);
            });

            $scope.$on('$destroy', $scope.destroy);
        },
        link: function (scope, element, attrs, ctrl) {

        }
    }
}]);

/*
 directive to invoke the ion range slider
 */
angular.module('acedIntervention.directives').directive('ionRangeSlider', function() {
    return {
        restrict: 'EA',
        scope:{min:'@',
            max:'@',
            type:'@sliderType',
            from:'=',
            to:'=',
            disable:'=',
            grid_snap:'=',
            grid:'@',
            prefix:'@',
            maxPostfix:'@',
            postfix:'@',
            onFinish:'='
        },
        template:'<input type="text"/>',
        replace:true,
        controller: function ($scope,$element) {
            var postfix = $scope.postfix || ' ';
            $($element).ionRangeSlider({
                min: $scope.min,
                max: $scope.max,
                type: $scope.type,
                from: $scope.from,
                to:$scope.to,
                disable:$scope.disable || false,
                grid_snap:$scope.grid_snap,
                grid:$scope.grid,
                prefix: $scope.prefix || '',
                maxPostfix: $scope.maxPostfix || '',
                postfix:" "+postfix,
                forceEdges:true,
                onFinish:function(data) {
                    $scope.onFinish(data);
                }
            });

            $scope.$on('$destroy',$scope.destroy);
        }
    }
});
/*
 * This directive manages the interval for on / off task and manages the countdown
 */
angular.module('acedIntervention.directives').directive('intervalManager', function () {
    return {
        templateUrl: 'template/intervalManager.html',
        scope: {
            intervalManagerConfig: '='
        },
        restrict: 'EA',
        controller: function ($scope) {
            var timerActive = false;
            $scope.task = {
                interval: 30
            };
            $scope.setInterval = function () {
                $scope.intervalManagerConfig.intervalSet = true;
                console.log($scope.task.interval);
                $scope.$emit('taskObservation-intervalSet', $scope.task.interval);
            };

            // when user starts observations, start the timer
            $scope.$on('taskObservation-timer-started',function(event,data){
                $scope.$broadcast('timer-start');
                timerActive = true;
            });

            $scope.$on('taskObservation-timer-stopped',function(event,data){
                timerActive = false;
                $scope.$broadcast('timer-stop');
            });

            $scope.$on('timer-stopped', function (event, data) {
                if (timerActive === true) {
                    $scope.$broadcast('timer-add-cd-seconds', $scope.task.interval);
                    $scope.$emit('intervalManager-intervalComplete', data);
                }
            });

            $scope.$on('$destroy', $scope.destroy);
        },
        link: function (scope, element, attrs, ctrl) {
        }
    }
});

// this directive scrolls the document to the anchor specified in the directive attribute
// the element should set attribute scroll-action
angular.module('acedIntervention.directives').directive('anchorScroll', function() {
    return {
        restrict: 'A',
        controller : function($scope) {
            $scope.$on('$destroy', $scope.destroy);
        },
        link: function(scope, elem, attrs) {
            if (attrs['scrollAction'] == 'click') {
                return elem.bind('click', function() {
                    var el;
                    el = document.getElementById(attrs['anchorScroll']);
                    return el.scrollIntoView();
                });
            }
        }
    };
});


