var observeOnOffTaskController = function($scope,studentService,utilsService,$rootScope) {
    var taskDataRecords = [],params,taskInterval;
    $rootScope.selectedAccountId = '3738973897983eihvov789h';
    $rootScope.selectedLocation = 'Room 101';
    // inits a taskDataItem
    function taskDataItem() {
        return {
            onTask: {
                active: {
                    student: false,
                    peer: false
                },
                passive: {
                    student: false,
                    peer: false
                }
            },
            offTask: {
                motor: {
                    student: false,
                    peer: false
                },
                verbal: {
                    student: false,
                    peer: false
                },
                passive: {
                    student: false,
                    peer: false
                }
            }
        }
    }

    $scope.taskDataItem = new taskDataItem();
    $scope.enableObservation = false;

    $scope.intervalManagerConfig = {
        enableObservation: $scope.enableObservation,
        intervalSet: false
    }

    $scope.$on('taskObservation-intervalSet', function(evt,interval){
        taskInterval = interval;
    });
    // listener for completion of timer
    $scope.$on('intervalManager-intervalComplete', function () {
        console.log($scope.taskDataItem);
        taskDataRecords.push($scope.taskDataItem);
        $scope.taskDataItem = new taskDataItem();
    });

    // listener for start of observation by user
    $scope.$on('observationManager-timerStarted', function (evt) {
        // user has started observation enable all buttons
        $scope.enableObservation = true;
        // trigger event to intervalManager to start time
        $scope.$broadcast('taskObservation-timer-started');
    });

    // listener for end of observation by user
    $scope.$on('observationManager-timerStopped', function (evt, data) {
        console.log('event received');
        console.log(data);

        // trigger event to intervalManager to stop
        $scope.$broadcast('taskObservation-timer-stopped');


        // save the records in the cloud
        params = {
            accountId:$rootScope.selectedAccountId,
            location:$rootScope.selectedLocation,
            totalObservationTime:data.millis,
            taskInterval:taskInterval,
            taskDataRecords:taskDataRecords
        };
        console.log(params);

        studentService.postTaskObservation(params,function(err,validationError,results){
            if (err) {
                // throw error
                utilsService.logger('error',err);
            } else if (validationError) {
                utilsService.logger('error',validationError);
            } else {
                // close modal
                $rootScope.goTo('student.manage.dashboard');
            }
        });

    });
}

angular.module('acedIntervention.controllers').controller('observeOnOffTaskController',['$scope','studentService','utilsService','$rootScope',observeOnOffTaskController]);

var observeFAndDController = function($scope,studentService,utilsService,$timeout,$rootScope) {
    $rootScope.selectedAccountId = '3738973897983eihvov789h';
    $rootScope.selectedLocation = 'Room 101';
    $scope.enableObservation = false;
    $scope.realTime = false;
    $scope.realTimeOptionSet = false;

    var params,behaviorMeasurementArray = [];
    params = {studentId:$rootScope.selectedAccountId};
    studentService.listTargetBehaviors(params,function(err,validationError,results){
        if (err) {
            // throw error
            utilsService.logger('error',err);
        } else if (validationError) {
            utilsService.logger('error',validationError);
        } else {
            $scope.behaviors = results.behaviors;
            _.each($scope.behaviors,function(behavior){
                behaviorMeasurementArray.push({
                    behavior:behavior.title,
                    frequency:0,
                    duration:[]
                });
            });
        }
    });

    $scope.$on('observationManager-timerStopped', function (evt, data) {
        console.log('observationManager-timerStopped event listener');
        console.log(data);
        $scope.enableObservation = false;
        // if any timers are running, stop it.
        $scope.$broadcast('durationTimer-stopTimer');
        // create one record for each behavior
        //{timestamp,accountId,totalObservationTime,targetBehavior,duration:[duration],frequency:9}

        $timeout(function(){
            var behaviorRecords=[];
            console.log(behaviorMeasurementArray);
            _.each(behaviorMeasurementArray,function(item){
                if (item.frequency > 0 || item.duration.length > 0) {
                    var record = {
                        accountId:$rootScope.selectedAccountId,
                        location:$rootScope.selectedLocation,
                        totalObservationTime:data.millis,
                        targetBehavior:item.behavior,
                        frequency:item.frequency,
                        duration:item.duration
                    }
                    behaviorRecords.push(record);
                }
            });

            studentService.postFrequencyObservation(behaviorRecords,function(err,validationError,results){
                if (err) {
                    // throw error
                    utilsService.logger('error',err);
                } else if (validationError) {
                    utilsService.logger('error',validationError);
                } else {
                    // close modal
                    $rootScope.goTo('student.manage.dashboard');
                }
            });
        },1000);
    });

    $scope.$on('observationManager-timerStarted', function (evt) {
        // user has started observation enable all buttons
        $scope.enableObservation = true;
    });


    $scope.$on('durationTimer-timerStopped', function (evt,durationData) {
        console.log(durationData);
        _.each(behaviorMeasurementArray,function(item){
            if (item.behavior == durationData.targetBehavior) {
                item.duration.push(durationData.duration)
            }
        });
    });

    $scope.$on('frequency-counterChanged', function (evt,frequencyData) {
        console.log(frequencyData);
        _.each(behaviorMeasurementArray,function(item){
            if (item.behavior == frequencyData.targetBehavior) {
                item.frequency = frequencyData.frequency
            }
        });

    });

    $scope.$on('toggleButton-changed',function(evt,realTime) {
        console.log('toggleButton-changed... event listener');
        $scope.realTime = realTime;
        $scope.realTimeOptionSet = true;
        console.log('realTime...'+$scope.realTime);
        console.log('realTimeOptionSet...'+$scope.realTimeOptionSet);
    });

    console.log($scope);
}


angular.module('acedIntervention.controllers').controller('observeFAndDController',['$scope','studentService','utilsService','$timeout','$rootScope',observeFAndDController]);



var observeAbcController = function($scope,studentService,utilsService,$rootScope) {
    $rootScope.selectedAccountId = '3738973897983eihvov789h';
    $rootScope.selectedLocation = 'Room 101';
    $scope.enableObservation = false;
    var params,abcItems;abcRecords=[]
    params = {studentId:$rootScope.selectedAccountId};
    studentService.listAbcs(params,function(err,validationError,results){
        if (err) {
            // throw error
            utilsService.logger('error',err);
        } else if (validationError) {
            utilsService.logger('error',validationError);
        } else {
            abcItems = results.abcItems;
            $scope.abcItems = angular.copy(abcItems)
        }
    });

    // format of each abc record
    //[b,a,c]
    $scope.recordABC = function () {
        var abcRecord={a:[],b:"",c:[]};
        console.log($scope.abcItems.abcBehaviors);
        _.each($scope.abcItems.abcBehaviors,function(behavior){
            if (behavior.checked== "true") { // set as HTML value
                abcRecord['b'] = behavior.title
            }
        });
        _.each($scope.abcItems.abcAntecedents,function(antecedent){
            if (antecedent.checked===true) {
                abcRecord['a'].push(antecedent.title);
            }
        });
        _.each($scope.abcItems.abcConsequences,function(consequence){
            if (consequence.checked===true) {
                abcRecord['c'].push(consequence.title);
            }
        });
        if ($scope.abcItems.userTyped.abcAntecedent.checked===true) {
            abcRecord['a'].push($scope.abcItems.userTyped.abcAntecedent.title);
        }
        if ($scope.abcItems.userTyped.abcConsequence.checked===true) {
            abcRecord['c'].push($scope.abcItems.userTyped.abcConsequence.title);
        }

        abcRecords.push(abcRecord);
        console.log(abcRecords);
        $scope.abcItems = angular.copy(abcItems)
    };

    $scope.$on('observationManager-timerStarted', function (evt) {
        // user has started observation enable all buttons
        $scope.enableObservation = true;
    });

    $scope.$on('observationManager-timerStopped', function (evt, data) {
        // save the records in the cloud
        params = {
            accountId:$rootScope.selectedAccountId,
            location:$rootScope.selectedLocation,
            totalObservationTime:data.millis,
            abcRecords:abcRecords
        };
        console.log(params);

        studentService.postAbcObservation(params,function(err,validationError,results){
            if (err) {
                // throw error
                utilsService.logger('error',err);
            } else if (validationError) {
                utilsService.logger('error',validationError);
            } else {
                // close modal
                $rootScope.goTo('student.manage.dashboard');
            }
        });

    });

    $scope.setUserTyped = function(type){
        if (type=='a' && $scope.abcItems.userTyped.abcAntecedent.title != '') {
            $scope.abcItems.userTyped.abcAntecedent.checked = true
        }
        if (type=='a' && $scope.abcItems.userTyped.abcAntecedent.title == '') {
            $scope.abcItems.userTyped.abcAntecedent.checked = false
        }
        if (type=='c' && $scope.abcItems.userTyped.abcConsequence.title != '') {
            $scope.abcItems.userTyped.abcConsequence.checked = true
        }
        if (type=='c' && $scope.abcItems.userTyped.abcConsequence.title == '') {
            $scope.abcItems.userTyped.abcConsequence.checked = false
        }
    }
}

